import React, { useState, useEffect } from 'react';
import { useAccount, useConnect, useDisconnect, useContractWrite, usePrepareContractWrite, useContractRead } from 'wagmi';
import { CONTRACT_ADDRESSES, CONTRACT_ABIS, CONTRACT_OWNER } from '../config/contracts';

function ConnectWallet() {
  const { connect, connectors, isLoading, pendingConnector } = useConnect();
  const { disconnect } = useDisconnect();
  const { address, isConnected } = useAccount();

  if (isConnected) {
    const isOwner = address?.toLowerCase() === CONTRACT_OWNER.toLowerCase();
    
    return (
      <div className="wallet-status">
        <p>: {address}</p>
        {isOwner && <p className="owner-badge"> </p>}
        <button onClick={() => disconnect()} className="disconnect-btn">
          
        </button>
      </div>
    );
  }

  return (
    <div className="wallet-status">
      <p>: </p>
      {connectors.map((connector) => (
        <button
          key={connector.id}
          onClick={() => connect({ connector })}
          disabled={!connector.ready || isLoading}
          className="connect-btn"
        >
          {isLoading && connector.id === pendingConnector?.id
            ? '...'
            : ` ${connector.name}`}
        </button>
      ))}
    </div>
  );
}

const Registration = () => {
  const { address } = useAccount();
  const [error, setError] = useState('');
  const [justRegistered, setJustRegistered] = useState(false);

  // 
  const { data: isRegistered, refetch: refetchRegistration, isError: isRegistrationError } = useContractRead({
    address: CONTRACT_ADDRESSES.MANAGEMENT,
    abi: CONTRACT_ABIS.MANAGEMENT,
    functionName: 'isVoterRegistered',
    args: [address],
    enabled: !!address,
    blockTag: 'latest', // 
    staleTime: 1000, // 1
  });

  // 
  const { data: isRegistrationOpen } = useContractRead({
    address: CONTRACT_ADDRESSES.MANAGEMENT,
    abi: CONTRACT_ABIS.MANAGEMENT,
    functionName: 'isRegistrationOpen',
    enabled: true,
    blockTag: 'latest',
  });

  // 
  const { config: registerConfig } = usePrepareContractWrite({
    address: CONTRACT_ADDRESSES.MANAGEMENT,
    abi: CONTRACT_ABIS.MANAGEMENT,
    functionName: 'register',
    args: [
      '0x', //  ringSig (bytes)
      '0x'  //  pkSet (bytes)
    ],
    enabled: !!address && !isRegistered && !isRegistrationError && isRegistrationOpen,
  });

  // 
  const { 
    write: registerVoter, 
    isLoading: isRegistering,
    isSuccess: registerSuccess
  } = useContractWrite({
    ...registerConfig,
    onSuccess: () => {
      setJustRegistered(true);
      setTimeout(() => {
        refetchRegistration(); // 
      }, 2000);
    },
    onError: (error) => {
      console.error(':', error);
      setError(error.shortMessage || error.message || '');
    }
  });

  const handleRegister = async () => {
    if (!address) {
      setError('');
      return;
    }

    if (isRegistrationError) {
      setError('');
      return;
    }

    if (!isRegistrationOpen) {
      setError('');
      return;
    }

    if (isRegistered) {
      setError('');
      return;
    }

    try {
      setError('');
      await registerVoter?.();
    } catch (err) {
      console.error(':', err);
      setError(err.shortMessage || err.message || '');
    }
  };

  // 
  useEffect(() => {
    setJustRegistered(false);
    setError('');
  }, [address]);

  //  justRegistered 
  useEffect(() => {
    if (registerSuccess && justRegistered) {
      const timer = setTimeout(() => {
        setJustRegistered(false);
      }, 3000); // 3""
      return () => clearTimeout(timer);
    }
  }, [registerSuccess, justRegistered]);

  return (
    <div className="registration-container">
      <h2></h2>
      
      <ConnectWallet />
      
      {!address ? (
        <p></p>
      ) : isRegistrationError ? (
        <div className="error">
          <p></p>
        </div>
      ) : !isRegistrationOpen ? (
        <div className="error">
          <p></p>
        </div>
      ) : isRegistered && !justRegistered ? (
        <div className="success-message">
          <p></p>
        </div>
      ) : (
        <div>
          <button 
            onClick={handleRegister}
            disabled={!address || isRegistering || !registerVoter || isRegistered}
          >
            {isRegistering ? '...' : ''}
          </button>
          
          {justRegistered && <p className="success"></p>}
          {error && <p className="error">{error}</p>}
        </div>
      )}
    </div>
  );
};

export default Registration;
