import React from 'react';
import { WagmiConfig, createConfig, configureChains, mainnet, useConnect, useDisconnect, useAccount } from 'wagmi';
import { publicProvider } from 'wagmi/providers/public';
import { MetaMaskConnector } from 'wagmi/connectors/metaMask';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { CONTRACT_OWNER } from './config/contracts';
import Registration from './components/Registration';
import Voting from './components/Voting';
import AdminPanel from './components/AdminPanel';
import './App.css';

//  hardhat 
const hardhatChain = {
  id: 31337,
  name: 'Hardhat',
  network: 'hardhat',
  nativeCurrency: {
    name: 'Ether',
    symbol: 'ETH',
    decimals: 18,
  },
  rpcUrls: {
    default: { http: ['http://127.0.0.1:8545'] },
    public: { http: ['http://127.0.0.1:8545'] },
  },
  blockExplorers: {
    default: { name: 'Etherscan', url: 'https://etherscan.io' },
  },
  testnet: true,
};

//  wagmi
const { chains, publicClient } = configureChains(
  [mainnet, hardhatChain],
  [publicProvider()]
);

const config = createConfig({
  autoConnect: true,
  publicClient,
  connectors: [
    new MetaMaskConnector({ chains })
  ]
});

//  React Query 
const queryClient = new QueryClient();

function App() {
  return (
    <WagmiConfig config={config}>
      <QueryClientProvider client={queryClient}>
        <div className="App">
          <header className="App-header">
            <h1></h1>
            <ContractOwnerInfo />
          </header>
          <main>
            <div className="container">
              <section className="section">
                <Registration />
              </section>
              <section className="section">
                <Voting />
              </section>
              <section className="section">
                <AdminPanel />
              </section>
            </div>
          </main>
        </div>
      </QueryClientProvider>
    </WagmiConfig>
  );
}

function ContractOwnerInfo() {
  return (
    <div className="contract-owner-info">
      <p>: {CONTRACT_OWNER}</p>
    </div>
  );
}

export default App;
