import React, { useState, useEffect } from 'react';
import { useAccount, useContractWrite, usePrepareContractWrite, useContractRead } from 'wagmi';
import { CONTRACT_ADDRESSES, CONTRACT_ABIS, CONTRACT_OWNER } from '../config/contracts';
import { ethers } from 'ethers';

const Voting = () => {
  const { address } = useAccount();
  const [selectedCandidate, setSelectedCandidate] = useState('');
  const [error, setError] = useState('');
  const [justVoted, setJustVoted] = useState(false);

  const candidates = ['A', 'B', 'C'];

  // 
  const { data: hasVoted, refetch: refetchVoteStatus } = useContractRead({
    address: CONTRACT_ADDRESSES.COUNTING,
    abi: CONTRACT_ABIS.COUNTING,
    functionName: 'submittedBallots',
    args: [address],
    enabled: !!address,
    blockTag: 'latest',
    staleTime: 1000,
  });

  // 
  const { data: isRegistered } = useContractRead({
    address: CONTRACT_ADDRESSES.MANAGEMENT,
    abi: CONTRACT_ABIS.MANAGEMENT,
    functionName: 'isVoterRegistered',
    args: [address],
    enabled: !!address,
    blockTag: 'latest',
    staleTime: 1000,
  });

  // 
  const { config: voteConfig } = usePrepareContractWrite({
    address: CONTRACT_ADDRESSES.COUNTING,
    abi: CONTRACT_ABIS.COUNTING,
    functionName: 'submitBallot',
    args: [
      ethers.encodeBytes32String(selectedCandidate), //  bytes32
      '0x00' // 
    ],
    enabled: !!address && !!selectedCandidate && isRegistered && !hasVoted,
  });

  // 
  const { 
    write: submitVote, 
    isLoading: isVoting,
    isSuccess: voteSuccess
  } = useContractWrite({
    ...voteConfig,
    onSuccess: () => {
      setJustVoted(true);
      setTimeout(() => {
        refetchVoteStatus();
      }, 2000);
    },
    onError: (error) => {
      console.error(':', error);
      setError(error.message || '');
    }
  });

  const handleVote = async () => {
    if (!selectedCandidate) {
      setError('');
      return;
    }

    if (!isRegistered) {
      setError('');
      return;
    }

    if (hasVoted) {
      setError('');
      return;
    }

    try {
      setError('');
      await submitVote?.();
    } catch (err) {
      setError(err.message || '');
    }
  };

  // 
  useEffect(() => {
    setJustVoted(false);
    setError('');
    setSelectedCandidate('');
  }, [address]);

  //  justVoted 
  useEffect(() => {
    if (voteSuccess && justVoted) {
      const timer = setTimeout(() => {
        setJustVoted(false);
      }, 3000);
      return () => clearTimeout(timer);
    }
  }, [voteSuccess, justVoted]);

  const isOwner = address?.toLowerCase() === CONTRACT_OWNER.toLowerCase();

  return (
    <div className="voting-container">
      <h2></h2>
      
      {!address ? (
        <p></p>
      ) : !isRegistered ? (
        <p></p>
      ) : isOwner ? (
        <div className="owner-dashboard">
          <h3> </h3>
          <p></p>
          {/*  */}
        </div>
      ) : hasVoted && !justVoted ? (
        <div className="success-message">
          <p></p>
        </div>
      ) : (
        <div>
          <label htmlFor="candidate-select"></label>
          <select
            id="candidate-select"
            value={selectedCandidate}
            onChange={(e) => setSelectedCandidate(e.target.value)}
            disabled={isVoting}
          >
            <option value=""></option>
            {candidates.map(candidate => (
              <option key={candidate} value={candidate}>
                {candidate}
              </option>
            ))}
          </select>
          
          <button 
            onClick={handleVote}
            disabled={!selectedCandidate || isVoting || !submitVote}
          >
            {isVoting ? '...' : ''}
          </button>
          
          {justVoted && <p className="success"></p>}
          {error && <p className="error">{error}</p>}
        </div>
      )}
    </div>
  );
};

export default Voting;
